body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html{
  background-color: #81d2c7;
}

.oreo {
  overflow:hidden;
  margin: 5px 20vw 20px 20vw;
  border: solid #36558f 5px;
  padding: 5px 2px;
}
.oreoForm{
  clear: left;
  display: block;
  margin-right: auto;
  margin-left: auto;
  font-size: 40px;
  width: 40px;
  border: solid #36558f;
  border-radius: 7px;
  margin-top: 5px;
  background-color: #e0e0e2;
}
.title{
  color: #36558f;
  font-size: 40px;
  text-align: center;
  font-weight: 700;
  padding-top: 10px;
}
h4 {
  position: fixed;
  color: #fe5d26;
  border-bottom:solid #fe5d26;
  display: inline-block;
  bottom: 10px;
  right: 10px;
  margin-right: 20vw
}

.result{
  color: #25201D;
  width: 60vw;
  margin: 0px 20vw;
  font-size: 20px;
  font-weight: 700;
}

.share-button {
  position: fixed;
  margin-left: 5vw;
  bottom: 10px;
}
.O {
  height: 100px;
  width: 200px;
  background-color: #25201D;
  border-radius: 100px / 50px;
  position: relative;   /*オレオの模様のために相対位置を設定*/
  border: solid #68605E 0.5px;
}
.pattern {
  height: 75px;
  width: 170px;
  border-radius: 130px / 60px;
  border: dashed #68605E;

  position: absolute;
  top: 3px;
  left: 12px;

  color: #68605E;
  text-align: center;

  font-family: 'Hiragino Kaku Gothic Pro','\30D2\30E9\30AE\30CE\89D2\30B4   Pro W3','\30E1\30A4\30EA\30AA',Meiryo,'\FF2D\FF33   \FF30\30B4\30B7\30C3\30AF',sans-serif;
  font-size: 25px;
  font-weight: 450;
  line-height: 65px;
}
.Re{
  border: solid #68605E 2.5px;
  height: 80px;
  width: 160px;
  border-radius: 100px / 50px;
  position: relative;   /*オレオの模様のために相対位置を設定*/
  left: 20px;
  background-color: #f0f8ff;
}
.shadow {
  height: 55px;
  width: 120px;
  border-radius: 100px / 50px;
  border-right: solid rgb(109, 96, 94, 0.3) 2px;

  position: absolute;
  top: 7px;
  right: 15px;
}
